
/**
 * this,
 * @protected
 * @param  {Object} obj   
 * @param  {String} action 
 * @return {Function}        
 */
function wrapBehavior(obj: object, action: string): Function {
  if (obj['_wrap_' + action]) {
    return obj['_wrap_' + action];
  }
  const method = e => {
    obj[action](e);
  };
  obj['_wrap_' + action] = method;
  return method;
}

export default wrapBehavior;
